.App {
  width: 100vw;
  height: 100vh;
  background: #f4f1e8 center calc(100% + 2px);
  background-repeat: no-repeat;
  background-size: contain;
  font-size: 4.5vw;
}

.findInfo {
  width: 100%;
  margin-bottom: 15vh;
  height: 10vh;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5em;
  color: #796f78;
  font-family: "Helvetica Neue", sans-serif;
  background: #fff;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #e1e1e1;
  text-transform: uppercase;
  -webkit-transform: translateY(45vh);
      -ms-transform: translateY(45vh);
          transform: translateY(45vh);
  -webkit-transition: 500ms -webkit-transform;
  transition: 500ms -webkit-transform;
  -o-transition: 500ms transform;
  transition: 500ms transform;
  transition: 500ms transform, 500ms -webkit-transform;
}

.findInfoStarted {
  -webkit-transform: translateY(5vh);
      -ms-transform: translateY(5vh);
          transform: translateY(5vh);
}

.smallInfoText {
  font-size: 0.6em;
  color: #A69FA3;
}

.foundCarrier {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100vw;
  bottom: -7vh;
  opacity: 0;
  -webkit-transition: 500ms opacity;
  -o-transition: 500ms opacity;
  transition: 500ms opacity;
  -webkit-transition-delay: 500ms;
       -o-transition-delay: 500ms;
          transition-delay: 500ms;
}

.foundCarrierStarted {
  opacity: 1;
}

.generalCarrier {
  -webkit-transition: 500ms opacity;
  -o-transition: 500ms opacity;
  transition: 500ms opacity;
  -webkit-transition-delay: 500ms;
       -o-transition-delay: 500ms;
          transition-delay: 500ms;
  opacity: 0;
}

.generalCarrierStarted {
  opacity: 1;
  height: 78vh;
}

.gameCarrier {
  width: calc(100vw - 50px);
  margin: 0px 24px;
  background: #fff;
  border: 1px solid #e1e1e1;
  border-radius: 3px;
  overflow: hidden;
  height: calc(100vw - 50px);
}

.gameCarrier span {
  text-align: center;
  letter-spacing: 10px;
  font-weight: bold;
}

.Game {
  width: 100%;
  height: 100%;
  opacity: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.GameStarted {
  opacity: 1;
}

.node {
  width: calc(100% / 8 - 2px);
  height: calc(100% / 8 - 2px);
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.4em;
  border: 1px solid #eaeaea;
  color: #796f78;
  -webkit-transition: 300ms all;
  -o-transition: 300ms all;
  transition: 300ms all;
}

.nodeEmpty {
  background: #fafafa;
}

.draggableNode {
  border: 1px solid #fff;
  margin: 0px;
  width: calc((100vw - 50px) / 8);
  height: calc((100vw - 50px) / 8);
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  bottom: 0px;
  text-align: center;
  letter-spacing: 0px;
  background: #fff;
  font-size: 1.4em;
  color: #444;
}

.highlighted1 {
  background: #7fadd0 !important;
  border: 1px solid #7fadd0 !important;
  color: #fff;
}

.highlighted2 {
  background: #e77676 !important;
  border: 1px solid #e77676 !important;
  color: #fff;
}

.highlighted3 {
  background: #3dc59c !important;
  border: 1px solid #3dc59c !important;
  color: #fff;
}

.highlighted4 {
  background: #eacd4f !important;
  border: 1px solid #eacd4f !important;
  color: #fff;
}

.highlighted5 {
  background: #9588d6 !important;
  border: 1px solid #9588d6 !important;
  color: #fff;
}

.draggableItemsHolder {
  height: 15vh;
  width: calc(100vw - ((100vw - 50px) / 8));
  margin: 20px 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding: 0px calc((100vw - 50px) / 16);
}

.draggableNodeCarrier {
  -webkit-transition: 0ms bottom, 0ms -webkit-transform;
  transition: 0ms bottom, 0ms -webkit-transform;
  -o-transition: 0ms transform, 0ms bottom;
  transition: 0ms transform, 0ms bottom;
  transition: 0ms transform, 0ms bottom, 0ms -webkit-transform;
  will-change: transform;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  position: relative;
  bottom: 0px;
}

.draggableNodeCarrier .clear {
  clear:both;
  width: 10px;
  height: 0px;
}

.invisible {
  opacity: 0;
}


.react-draggable-dragging {
  -webkit-transition: 200ms bottom linear, 0ms -webkit-transform !important;
  transition: 200ms bottom linear, 0ms -webkit-transform !important;
  -o-transition: 0ms transform, 200ms bottom linear !important;
  transition: 0ms transform, 200ms bottom linear !important;
  transition: 0ms transform, 200ms bottom linear, 0ms -webkit-transform !important;
  bottom: 9vh;
}

.nodeFading {
  -webkit-transition: 200ms bottom linear, 200ms -webkit-transform !important;
  transition: 200ms bottom linear, 200ms -webkit-transform !important;
  -o-transition: 200ms transform, 200ms bottom linear !important;
  transition: 200ms transform, 200ms bottom linear !important;
  transition: 200ms transform, 200ms bottom linear, 200ms -webkit-transform !important;
}

.draggableNodeWithOutBorder {
  border: 1px solid #fff;
  -webkit-transition: 300ms all !important;
  -o-transition: 300ms all !important;
  transition: 300ms all !important;
}


.react-draggable-dragging .draggableNode {
  border: 1px solid #fff;
  background: #fff;
  margin: 0px;
}

.react-draggable-dragging {
  position: relative;
  z-index: 999;
}


.found {
  width: 14px;
  height: 14px;
  border-radius: 9px;
  background: #88c18e;
  margin: 5px;
  border: 2px solid #fff;
  -webkit-transition: 300ms all;
  -o-transition: 300ms all;
  transition: 300ms all;
}

.notfound {
  width: 15px;
  height: 15px;
  border: 1px solid #e1e1e1;
  border-radius: 9px;
  background: white;
  margin: 5px;
  -webkit-transition: 300ms all;
  -o-transition: 300ms all;
  transition: 300ms all;
}

.pixelFixerCarrier {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
}

.pixelFixer {
  width: 1px;
  height: 1px;
  overflow: hidden;
  background: #ddd;
  position: absolute;
  z-index: 1;
}

.pixelFixer.tl {
  left: -1px;
  top: -1px;
}

.pixelFixer.tr {
  right: -1px;
  top: -1px;
}

.pixelFixer.bl {
  left: -1px;
  bottom: -1px;
}

.pixelFixer.br {
  right: -1px;
  bottom: -1px;
}

.highlighted1 .pixelFixer,
.highlighted2 .pixelFixer,
.highlighted3 .pixelFixer,
.highlighted4 .pixelFixer,
.highlighted5 .pixelFixer {
  display: none;
}

@-webkit-keyframes totorial {
  0%   { bottom: 5vh; left: 25vw; }
  80%   { bottom: 42vh; left: 65vw;  }
  100% { bottom: 5vh; left: 25vw;  }
}

@keyframes totorial {
  0%   { bottom: 5vh; left: 25vw; }
  80%   { bottom: 42vh; left: 65vw;  }
  100% { bottom: 5vh; left: 25vw;  }
}

.tutorial {
  position: fixed;
  bottom: 5vh;
  left: 25vw;
  z-index: 999;
  opacity: 0;
  -webkit-transition: 500ms all;
  -o-transition: 500ms all;
  transition: 500ms all;
  -webkit-animation: totorial 2s infinite;
          animation: totorial 2s infinite;
}

.tOpen {
  opacity: 1;
}



