@font-face {
  font-family: 'Varela Round';
  font-style: normal;
  font-weight: 400;
  src: local('Varela Round Regular'), local('VarelaRound-Regular'), url('./data/font.woff') format('woff');
}

* {
  -webkit-user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body {
  margin: 0;
  padding: 0;
  font-family: "Varela Round", sans-serif;
  font-weight: 700;
  font-size: 20px;
  height: 100%;
  background: #f4f1e8;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  overflow: hidden !important;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  overflow: hidden;
  -webkit-overflow-scrolling: auto;
  /* Kill touch scrolling */
  -webkit-touch-callout: none;
  -ms-touch-action: none;
      touch-action: none;
}

img {
    pointer-events: none !important;
    /* Kill "Save image - Copy" Menu */
}

#level {
  position: absolute;
  top:100px;
  left: 0px;
}



